import React from 'react'
//condition true? false:
function cart({allData}) {
    return(
    <div>
    {allData.map((element)=>{
   
                return(
                    <div>
                    <h2>my Name is :{element.FirstName} {element.LastName} </h2>
                    <h5>my color is :{element.Color} </h5>
                    <h5>my id is :{element.id} </h5>
                    <hr></hr>
                    </div>
                )
        })}
    </div>
  )
}

export default cart;