
import React, { useEffect, useState } from 'react'
import Navbar from '../Home/Navbar'
import { Link, useParams } from 'react-router';
import axios from 'axios';

function AllSingleproduct() {
    const {id} = useParams();
    const [text,setText] = useState([]);

  useEffect( ()=>{
      axios.get("https://dummyjson.com/products")
      .then((element)=>{
         setText(element.data.products);
      });
  },[]);

  return (
    <div>
        <Navbar/>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <div className="container">
        {text.map((ahmed)=>{
            return ahmed.id == id?(
                <div className="row">
                   <div className="col-md-6 text-center">
                    <img src={ahmed.images[0]} className="w-75"></img>
                    </div> 
                    <div className="col-md-6 text-center mt-5 pt-5">
                        <h3>{ahmed.title}</h3>
                        <h4 className="mt-4">category:{ahmed.category}</h4>
                        <h4 className="mt-4">Brand:{ahmed.brand}</h4>
                        <h4 className="mt-4">Rating:{ahmed.rating}</h4>
                        <h4 className="mt-4">Discount:{ahmed.discountpercentage}of 100%</h4>
                        <h4 className="mt-4">stock:{ahmed.stock}</h4>
                        <h4 className="mt-4">Price:{ahmed.price}</h4>
                        <h4 className="mt-4">id:{ahmed.id}</h4>
                        <Link to="/products"className="btn btn-outline-dark btn-block">Back</Link>
                    </div>
                </div>
            ):null
        })}
        </div>
    </div>
  )
}

export default AllSingleproduct;