
import React, { useEffect, useState } from 'react'
import Navbar from '../Home/Navbar'
import { Link } from 'react-router';
import axios from 'axios';

function AllProducts() {

const [text,setText] = useState([]);
const [search,setsearch] = useState(0);

useEffect( ()=>{
    axios.get("https://dummyjson.com/products")
    .then((element)=>{
       setText(element.data.products);
    });
},[]);

const getsearch=(event)=>{
setsearch(event.target.value);
}
  return (
    <div>
        <Navbar/>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <div className="container">
        <div className="row">
          <div className="col-md-10 m-auto">
            <input onChange={getsearch} type="text" className="form-control"placeholder="search"></input>
          </div>
         {text.map((ahmed)=>{
         return search==0? (
              <div className="col-md-4 text-center mb-5 ">
                <img src={ahmed.images[0]} className="w-100"></img>
                <h5>{ahmed.title}</h5>
                <h5>{ahmed.price}$</h5>
                <h5>{ahmed.id}</h5>
                <Link to={`/singleproduct/${ahmed.id}`} className="btn btn-outline-dark mt-3">Details</Link>
              </div>
         ): ahmed.id == search ? (
           <div className="col-md-4 text-center mb-5 ">
                <img src={ahmed.images[0]} className="w-100"></img>
                <h5>{ahmed.title}</h5>
                <h5>{ahmed.price}$</h5>
                <h5>{ahmed.id}</h5>
             <Link to={`/singleproduct/${ahmed.id}`} className="btn btn-outline-dark mt-3">Details</Link>
              </div>
         ):ahmed.title.toLowerCase().includes(search.toLowerCase()) ?(
           <div className="col-md-4 text-center mb-5 ">
                <img src={ahmed.images[0]} className="w-100"></img>
                <h5>{ahmed.title}</h5>
                <h5>{ahmed.price}$</h5>
                <h5>{ahmed.id}</h5>
              <Link to={`/singleproduct/${ahmed.id}`} className="btn btn-outline-dark mt-3">Details</Link>
              </div>
         ):null
         })}

          </div>
        </div>
    </div>
  )
}

export default AllProducts;